import React from 'react';
import Badge from '../badge/badge';
export enum AvatarSize {
  small = 'small',
  xsmall = 'xsmall',
  big = 'big',
}

export interface IAvatarProps {
  /**
   * Title of avatar image
   */
  title?: string;
  /**
   * Subtitle of avatar image
   */
  subtitle?: string;

  /**
   * Set the xsmall size
   */
  xsmall?: boolean;
  /**
   * Set the small size
   */
  small?: boolean;
  /**
   * Set the big size
   */
  big?: boolean;

  /**
   * Set if Avatar is disabled
   */
  disabled?: boolean;
  /**
   * Function that will ocurred when user click on avatar image
   */
  onClick?: () => void;
  badge?: number | string;
  /**
   * Additional or alternative styling
   */
  className?: string;

  [others: string]: any;
}

const Avatar: React.FC<IAvatarProps> = (props) => {
  const {xsmall, small, big, disabled, title, subtitle, badge, className, children, onClick, ...rest} = props;

  const getSize = () => {
    if (xsmall) {
      return AvatarSize.xsmall;
    }
    if (small) {
      return AvatarSize.small;
    }
    if (big) {
      return AvatarSize.big;
    }
    return undefined;
  };

  const renderAvatarWrapper = () => {
    const avatar = badge ? (
      <div className="avatar-container">
        {renderAvatar()}
        {xsmall ? <Badge /> : <Badge>{badge}</Badge>}
      </div>
    ) : (
      <>{renderAvatar()}</>
    );

    if (title) {
      if (subtitle) {
        return (
          <>
            {avatar}
            <div className="avatar-text-wrapper">
              <span className="avatar-title">{title}</span>
              <span className="avatar-subtitle">{subtitle}</span>
            </div>
          </>
        );
      }
      return (
        <>
          {avatar}
          <span className="avatar-title">{title}</span>
        </>
      );
    }

    return avatar;
  };

  const renderAvatar = () => {
    if (children) {
      const _child = children as any;
      const _sizeStr = getSize() ? `_${getSize()}` : '';

      if (typeof onClick === 'function') {
        return (
          <button type="button" className={`avatar${_sizeStr}`} onClick={rest.onClick}>
            {_child}
          </button>
        );
      }
      return React.cloneElement(_child, {
        ..._child.props,
        className: `avatar${_sizeStr} ${_child.props.className || ''}`,
      });
    }
  };

  return (
    <div className={`avatar-wrapper${disabled ? '_disabled' : ''} ${className || ''}`} {...rest}>
      {renderAvatarWrapper()}
    </div>
  );
};

export default Avatar;
